import React from 'react';
import { MainBody as IMainBody } from '../types';
import { config } from './config';

const { bottomBoundary, topBoundary, berthLine } = config.design;
const mb = (1 + bottomBoundary - topBoundary) * 100;
const mbOfLast = 0;

interface Props extends IMainBody {
  isLast?: boolean;
}

export const MainBody: React.FC<Props> = ({ brief, sub, sectons, isLast }) => {
  return (
    <div style={{ marginBottom: `${isLast ? mbOfLast : mb}vh` }}>
      {sub && <h2 className="mb-micro-8 type-subheading">{sub}</h2>}
      {brief && <h1 className="type-heading whitespace-pre-line">{brief}</h1>}
      {sectons &&
        sectons.map((section, i) => (
          <Section key={i} label={section.name} content={section.value} />
        ))}
    </div>
  );
};

const Section: React.FC<{ label: string; content: string }> = ({ label, content }) => (
  <div className="mt-micro-32">
    <h3 className="type-label uppercase">{label}</h3>
    <p className="mt-micro-8 type-body">{content}</p>
  </div>
);
